<template>
  <div class="cont">
    <v-alert color="#1f7b8a" class="header" dark dense prominent>
      <p class="text-center">فيديوهات الدروس</p>
    </v-alert>
    <div class="filter">
      <label>الصف الدراسي</label>
      <select v-model="stage" class="form-control">
        <option selected value="1">الصف الأول الاعدادي</option>
        <option selected value="2">الصف الثاني الاعدادي</option>
        <option selected value="3">الصف الثالث الاعدادي</option>
        <option selected value="4">الصف الأول الثانوي</option>
        <option selected value="5">الصف الثاني الثانوي</option>
        <option selected value="6">الصف الثالث الثانوي</option>
      </select>
      <br />

      <div class="field">
        <label>الدورة</label>
        <select v-model="section" class="form-control">
          <option value="all">الكل</option>
          <option v-for="section in sections" :key="section._id" :value="section.number">
            {{ section.name }}
          </option>
        </select>
      </div>
    </div>

    <hr />
    <loading v-if="loading" />
    <div v-if="!loading">
      <hr />
      <div class="videos_section">
        <div v-for="vid in videos" :key="vid._id" class="video_card">
          <div class="video_info">
            <div class="video_desc">
              <h2>{{ vid.section | section(vid.stage) }}</h2>
              <hr />
              <h1>{{ vid.name }}</h1>
              <p style="color: #999">{{ vid.showDate | moment("dddd, MMMM Do YYYY") }}</p>
              <hr />
              <h3 class="">فيديو رقم {{ vid.number }}</h3>
              <p class="type">{{ vid.stage | stage }}</p>
            </div>
            <hr />
            <div class="video_options">
              <div class="option" @click="() => edit(vid._id)">
                <i class="fa fa-pencil">تعديل</i>
              </div>
              <div class="option" @click="() => deleteVid(vid._id, vid.stage)">
                <i class="fa fa-trash">ازالة</i>
              </div>
              <div
                v-if="lessonCodes"
                class="option"
                @click="$router.push(`/dashboard/lessonCodes/${vid._id}/${vid.stage}`)"
              >
                <i class="fa fa-trash">الأكواد</i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "../../components/loading.vue";
export default {
  components: {
    Loading,
  },
  created() {
    if (this.$route.params.section && this.$route.params.stage) {
      this.section = this.$route.params.section;
      this.stage = this.$route.params.stage;
    }
    this.getVideos();
    axios.get("/getSections").then((res) => {
      this.sections = res.data.sections;
    });
  },
  data() {
    return {
      videos: [],
      id: "",
      video: {},
      ready: false,
      loading: false,
      stage: "3",
      section: "all",
      sections: [],
    };
  },
  methods: {
    getVideos() {
      this.loading = true;
      axios
        .get(
          "/fetchVideos/" +
            localStorage.getItem("userId") +
            "/" +
            this.stage +
            "/" +
            this.section
        )
        .then((res) => {
          this.videos = res.data.videos;
          this.loading = false;
        });
    },
    edit(id) {
      this.$router.push({ path: "/dashboard/uploadVideo/" + id });
    },
    deleteVid(id, stage) {
      const confirmed = confirm("هل متاكد من مسح الدرس نهائيا ؟؟؟");
      if (confirmed) {
        axios("/deleteVideo/" + id + "/" + stage).then((res) => {
          this.videos = res.data.videos;
        });
      }
    },
    play(vid) {
      // this.$refs.modal_button.click();
      // this.video = vid;
      // this.ready = true;
    },
  },
  computed: {
    lessonCodes() {
      return this.$store.getters.adminData.lessonCodes;
    },
  },
  watch: {
    stage(val) {
      this.getVideos();
    },
    section(val) {
      this.getVideos();
    },
  },
};
</script>

